.ops-container {
	
	position:relative;
	display:block;
	padding:0;
	margin:0;
	height:100%;
	width:100%;
	transform: translate3d(0,0,0);
}

.ops-page {
	width: 100%;
	height: 100%;
	position: relative;
	overflow: hidden;
}

.ops-navigation {
	position: absolute;
	margin: 0;
	padding: 0;
	right: 12px;
	top: 50%;
	z-index: 2;
	list-style: none;
	float: right;
	-webkit-transform: translateY(-50%);
		-ms-transform: translateY(-50%);
			transform: translateY(-50%);
}

.ops-navigation li {
	text-align: center;
	margin-top: 1em;
	margin-bottom: 1em;
	padding: 0;
}

.ops-navigation a {
	display: block;
	width: 10px;
	height: 10px;
}

.ops-navigation a:before {
	display: block;
	content: ' ';
	width: 10px;
	height: 10px;
	background: #cc9e13 ;
	border-radius: 100%;
}

.ops-navigation a.active {
	width: 12px;
	height: 12px;
}
.ops-navigation a.active:before {
	width: 12px;
	height: 12px;
	background: #b43620 ;
	border: 1px solid #fff;
	left: -2px;
	position: relative;
}

.mySlides {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.slideshowSection {
	object-fit: cover; 
	width: 100%;
	height: auto;
	display: block;
	overflow: hidden;
}

.slideshowContent{
	position: relative;
	width: 100%;
	height: 100%;
}

.mainTitle {
