	font-size: 9rem;
	text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.firstSection {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	object-fit: cover;
}

.popularitySection {
	width: 100%;
	height: auto;
	object-fit: cover;
	justify-content: center;
}

.popularitySectionSpecial {
	height: auto !important;
}

.popularitySectionContent {
	display: flex;
	height: 100%;
	align-items: center;
}

.popularitySectionContain {
	height: 100%;
}

.popularitySectionText {
	width: auto;

}

.popularitySectionImg {
	display: flex !important;
	justify-content: center !important;
	height: 100%;
	padding: 20px;
